body {
  margin: 0;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: #F5F4EF;
}
img {
  max-width: 100%;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}
h1 {
  font-weight: 600;
}
.container {
  max-width: 1152px;
  margin: 0 auto;
  padding: 0 var(--offset-page);
}
:root {
  --transition-duration: 250ms;
  --transition-function: ease-in-out;
  --color-text: #212121;
  --offset-page-top: 12px;
  --offset-page: 12px;
}
@media (min-width: 768px) {
  :root {
    --offset-page: 32px;
    --offset-page-top: 13px;
  }
}
@media (min-width: 990px) {
  :root {
    --offset-page: 64px;
  }
}
@font-face {
  font-weight: 400;
  font-family: 'Montserrat';
  font-style: normal;
  src: url(../fonts/Montserrat-Regular.woff2) format('woff2'), url(../fonts/Montserrat-Regular.woff) format('woff');
  font-display: swap;
}
@font-face {
  font-weight: 600;
  font-family: 'Montserrat';
  font-style: normal;
  src: url(../fonts/Montserrat-SemiBold.woff2) format('woff2'), url(../fonts/Montserrat-SemiBold.woff) format('woff');
  font-display: swap;
}
@font-face {
  font-weight: 600;
  font-family: 'Random Grotesque Standard';
  font-style: normal;
  src: url(../fonts/RG-StandardSemibold.woff2) format('woff2'), url(../fonts/RG-StandardSemibold.woff) format('woff');
  font-display: swap;
}
.header {
  border-bottom: 1px solid #DDDCD6;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: var(--offset-page-top);
  padding-bottom: var(--offset-page-top);
}
.header__logo img {
  width: 82px;
  height: 47px;
}
@media (min-width: 768px) {
  .header__logo img {
    width: 116px;
    height: 66px;
  }
}
@media (max-width: 767px) {
  .header__nav-toggle {
    position: relative;
    display: block;
    width: 65px;
    height: 16px;
    padding: 0;
    background-color: transparent;
    border: 1px solid transparent;
    border-top-color: #000;
    border-bottom-color: #000;
    appearance: none;
  }
  .header__nav-toggle::before,
  .header__nav-toggle::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-top: 1px solid #000;
    transform: translateY(-50%);
  }
  .header__nav-toggle::after {
    border-top: 0;
  }
}
@media (min-width: 768px) {
  .header__nav-toggle {
    display: none;
  }
  .header__right,
  .header__logo {
    flex-shrink: 0;
    width: 200px;
  }
  .header__right {
    margin-right: -10px;
    margin-left: 10px;
    text-align: right;
  }
}
.header__link {
  --line-height: 17px;
  position: relative;
  display: inline-block;
  height: var(--line-height);
  padding: 4px 10px 5px;
  overflow: hidden;
  font-weight: 600;
  font-size: 14px;
  line-height: var(--line-height);
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
}
.header__link span {
  display: block;
  height: var(--line-height);
  transition: opacity var(--transition-duration) var(--transition-function), transform var(--transition-duration) var(--transition-function);
}
.header__link .header__link-hover {
  opacity: 0;
}
.header__link .header__link-view {
  opacity: 1;
}
.header__link:hover span {
  transform: translateY(-100%);
}
.header__link:hover .header__link-hover {
  opacity: 1;
}
.header__link:hover .header__link-view {
  opacity: 0;
}
.nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .nav__right,
  .nav {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav__list {
    justify-content: center;
    width: fit-content;
    margin: 0 auto;
  }
}
.list-links {
  --offset-link: 4px;
  display: inline-flex;
  flex-wrap: wrap;
  margin: 0;
  margin-right: calc(var(--offset-link)*-1);
}
@media (min-width: 768px) {
  .list-links {
    --offset-link: 8px;
  }
}
.list-links a {
  display: block;
  margin-right: var(--offset-link);
  margin-bottom: var(--offset-link);
  padding: 4px 10px 5px;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: inherit;
  text-transform: lowercase;
  text-decoration: none;
  border: 1px solid #212121;
  border-radius: 12px;
}
@media (min-width: 990px) {
  .list-links a {
    font-size: 14px;
    line-height: 17px;
  }
}
.main__header {
  padding-top: 32px;
  padding-bottom: 24px;
}
@media (min-width: 768px) {
  .main__header {
    display: flex;
  }
}
@media (min-width: 990px) {
  .main__header {
    padding-top: 54px;
    padding-bottom: 40px;
  }
}
@media (min-width: 990px) {
  .main__header-right {
    margin-left: auto;
    max-width: 277px;
  }
}
.main__header-title {
  position: relative;
  width: fit-content;
  margin: 0 0 12px;
  padding-right: 24px;
  font-weight: 600;
  font-size: 78px;
  line-height: 108%;
  line-height: 84px;
  font-family: 'Random Grotesque Standard';
  text-transform: uppercase;
  font-style: normal;
  letter-spacing: -0.01em;
}
@media (min-width: 768px) {
  .main__header-title {
    margin-bottom: 0;
    margin-right: 15px;
    font-size: 125px;
  }
}
@media (min-width: 990px) {
  .main__header-title {
    margin-right: 30px;
  }
}
@media (min-width: 1280px) {
  .main__header-title {
    font-size: 184px;
    line-height: 89%;
  }
}
.main__header-title sup {
  position: absolute;
  top: 0;
  right: 0;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}
.main__header-subtitle {
  margin: 0 0 32px;
  font-size: 12px;
  line-height: 15px;
}
@media (min-width: 768px) {
  .main__header-subtitle {
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (min-width: 1280px) {
  .main__header-subtitle {
    margin-bottom: 36px;
  }
}
.work {
  overflow: hidden;
}
.work__container {
  padding-bottom: 64px;
}
@media (min-width: 990px) {
  .work__container {
    padding-bottom: 84px;
  }
}
.work__slider__list {
  margin: 0 0 12px;
  padding: 0;
}
@media (min-width: 768px) {
  .work__slider__list {
    margin-bottom: 22px;
  }
}
.work__slider {
  overflow: visible;
}
.work__slider__item img {
  display: block;
}
.work__slider__button-wrap {
  text-align: center;
}
@media (min-width: 768px) {
  .work__slider__button-wrap {
    text-align: right;
    padding-right: calc(178px - var(--offset-page));
  }
}
.button-arrow {
  overflow: hidden;
  position: relative;
  display: inline-block;
  padding: 0;
  background-color: transparent;
  border: 1px solid var(--color-text);
  border-radius: 12px;
  appearance: none;
  width: 65px;
  height: 21px;
}
@media (min-width: 768px) {
  .button-arrow {
    width: 74px;
    height: 24px;
  }
}
.button-arrow::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-text);
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: 21px auto;
}
@media (min-width: 768px) {
  .button-arrow::before {
    mask-size: 24px auto;
  }
}
.button-arrow--left::before {
  mask-image: url(../img/arrow-left.svg);
}
.button-arrow--right::before {
  mask-image: url(../img/arrow-right.svg);
}
.portfolio__title {
  position: relative;
  max-width: 660px;
  font-weight: 600;
  font-size: 48px;
  line-height: 1;
  font-family: 'Stolzl', Arial, Helvetica, sans-serif;
}
.portfolio__title-animate {
  position: relative;
  display: block;
  height: 48px;
  overflow: hidden;
}
.portfolio__title-animate span {
  --duration: 20s;
  position: absolute;
  top: 0;
  left: 0;
  perspective: 100px;
  opacity: 0;
  animation-name: portfolio-title;
  animation-duration: var(--duration);
  animation-timing-function: var(--transition-function);
  animation-iteration-count: infinite;
}
.portfolio__title-animate span:nth-child(2) {
  animation-delay: calc(var(--duration)/3);
}
.portfolio__title-animate span:nth-child(3) {
  animation-delay: calc(var(--duration)/3 * 2);
}
@keyframes portfolio-title {
  0% {
    transform: rotateX(90deg) translateY(100%);
    opacity: 0;
  }
  1% {
    transform: rotateX(0) translateY(0);
    opacity: 1;
  }
  32% {
    transform: rotateX(0) translateY(0);
    opacity: 1;
  }
  33% {
    transform: rotateX(90deg) translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: rotateX(90deg) translateY(-100%);
    opacity: 0;
  }
}
.services__content {
  position: relative;
  display: flex;
  align-items: flex-start;
}
.services__left {
  position: sticky;
  top: 50vh;
}
.services__right span {
  display: block;
}
.services__button-arrow {
  opacity: 0;
  transition: opacity var(--transition-duration) var(--transition-function);
}
.services__right span:hover .services__button-arrow {
  opacity: 1;
}
